<template lang="html">
  <div>
    <div class="col-xs-12 filter-group p-l-15">
      <div class="row d-none d-sm-none d-md-none d-lg-flex">
        <div class="col-lg-3 col-md-6 col-sm-6 filter-col">
          <el-select
            class="full-width"
            v-model="currentFilter"
            default-first-option
          >
            <el-option
              v-for="(item, index) of rules"
              :key="item.value"
              :label="item.name"
              :value="item"
            >
            </el-option>
          </el-select>
        </div>
        <div class="col-lg-3 col-md-6 col-sm-6 filter-col">
          <el-select
            class="full-width"
            v-if="currentFilter"
            v-model="currentOpe"
            default-first-option
          >
            <el-option
              v-for="op of currentFilter.ops"
              :key="op.value"
              :label="op.name"
              :value="op"
              >
            </el-option>
          </el-select>
        </div>
        <div class="col-lg-3 col-md-6 col-sm-6 filter-col">
          <el-select
            class="full-width"
            v-if="currentFilter.type === 'select'"
            v-model="selectedOption"
            @keyup.enter.native="addFilter"
            default-first-option
          >
            <el-option
              v-for="fv of filterValues"
              :key="fv.value"
              :label="fv.title"
              :value="fv"
              >
            </el-option>
          </el-select>

          <el-input
            class="full-width"
            v-if="currentFilter.type !== 'select'"
            :type="currentFilter.type == 'number' ? 'number' : 'text'"
            v-model="inputValue"
            @keyup.enter.native="addFilter"
            ref="saveTagInput"
            placeholder="Nhập giá trị"
          ></el-input>
        </div>
        <div class="col-lg-3 col-md-6 col-sm-6 filter-col">
          <p-button class="full-width" @click="addFilter">
            <i class="fa fa-plus"></i>
             Thêm Điều kiện
          </p-button>
        </div>
      </div>
      <div class="d-flex d-sm-flex d-md-flex d-lg-none d-xl-none m-t-4">
        <el-button class="full-width" type="primary" @click="modal.display=true">
          <i class="fa fa-plus"></i>
          Bộ lọc
        </el-button>
      </div>
    </div>
    <div class="col-xs-12" v-if="tags.length > 0">
      <el-tag
        v-for="tag in tags"
        :key="tag.name + tag.ope + tag.value"
        closable @close="handleClose(tag)"
        :type="'filter'">
          <b>{{tag.name}}</b>
          {{tag.ope}}
          <span class="value">{{tag.type == 'number' ? $util.formatMoney(tag.value) : `"${tag.value}"`}}</span>
      </el-tag>
    </div>
    <modal :show.sync="modal.display" headerClasses="justify-content-center">
      <h4 slot="header" class="title title-up m-0">Thêm bộ lọc</h4>
      <div class="row">
        <div class="col-sm-12 m-b-5">
          <el-select
            class="full-width"
            v-model="currentFilter"
            default-first-option
          >
            <el-option
              v-for="(item, index) of rules"
              :key="item.value"
              :label="item.name"
              :value="item"
            >
            </el-option>
          </el-select>
        </div>
        <div class="col-sm-12 m-b-5">
          <el-select
            class="full-width"
            v-if="currentFilter"
            v-model="currentOpe"
            default-first-option
          >
            <el-option
              v-for="op of currentFilter.ops"
              :key="op.value"
              :label="op.name"
              :value="op"
            >
            </el-option>
          </el-select>
        </div>
        <div class="col-sm-12 m-b-5">
          <el-select
            class="full-width"
            v-if="currentFilter.type === 'select'"
            v-model="selectedOption"
            @keyup.enter.native="addFilter"
            default-first-option
          >
            <el-option
              v-for="fv of filterValues"
              :key="fv.value"
              :label="fv.title"
              :value="fv"
            >
            </el-option>
          </el-select>

          <el-input
            class="full-width"
            v-if="currentFilter.type !== 'select'"
            :type="currentFilter.type == 'number' ? 'number' : 'text'"
            v-model="inputValue"
            @keyup.enter.native="addFilter"
            ref="saveTagInput"
            placeholder="Nhập giá trị"
          ></el-input>
        </div>
      </div>
      <div class="text-right">
        <p-button @click.native="modal.display = false">Hủy bỏ</p-button>
        <p-button type="primary" @click="addFilter">Xác nhận</p-button>
      </div>
    </modal>
  </div>
</template>

<script>
  import {Select, Option, Button, Tag, Input, Message} from 'element-ui'
  import Modal from './Modal'

  export default {
    components: {
      ElSelect: Select,
      ElOption:Option,
      ElButton:Button,
      ElTag:Tag,
      ElInput:Input,
      Modal
    },
    props: ['rules'],
    data() {
      return {
        currentOpe: {},
        selectedOption: {},
        currentFilter: {},
        filterValues: [],
        tags: [],
        inputValue: '',
        output: [],
        modal: {
          display: false
        }
      };
    },
    computed: {
      currentValue() {
        if (this.currentFilter.type === 'select') {
          return this.selectedOption.value
        }
        return this.inputValue
      },
      currentLabel() {
        if (this.currentFilter.type === 'select') {
          return this.selectedOption.title
        }
        return this.inputValue
      }
    },
    watch: {
      currentFilter(newVal, oldVal) {
        if (newVal.type === 'select') {
          this.filterValues = newVal.values;
          this.selectedOption = newVal.values[0];
        } else {
          this.filterValues = [];
        }
        this.currentOpe = newVal.ops[0];
      }
    },
    methods: {
      handleClose(tag) {
        let index = this.tags.indexOf(tag);
        this.tags.splice(index, 1);
        this.output.splice(index, 1);
        this.$emit('filter-change', this.output);
      },
      addFilter() {
        if (!this.currentLabel){
          Message({
            message: 'Vui lòng nhập giá trị lọc',
            type: 'error'
          });
          return;
        }
        let tag = {
          filter: this.currentFilter.value,
          type: this.currentFilter.type,
          name: this.currentFilter.name,
          ope: this.currentOpe.name,
          opeValue: this.currentOpe.value,
          value: this.currentLabel
        }
        tag.key = tag.name + tag.ope + tag.value;
        this.tags = this.tags.filter((x) => {
          return x.name != tag.name;
        });

        this.output = this.output.filter((x) => {
          return x.filter != this.currentFilter.value;
        });

        let res = {
          filter: this.currentFilter.value,
          ope: this.currentOpe.value,
          value: this.currentValue,
          type: this.currentFilter.type
        }
        this.output.push(res);
        this.tags.push(tag);
        this.$emit('filter-change', this.output);
        this.selectedOption = '';
        this.inputValue = '';
      }
    },
    mounted(){
      this.currentFilter = this.rules[0];
    }
  }
</script>


