<template>
  <div class="row m-0">
    <div class="col-sm-12 mt-2 mb-2">
      <el-dropdown v-if="checkSelect" :style="dwStyle" class="dropdown-check-table">
        <el-button type="primary" :style="{'width': dwStyle.width}" >
          ({{checkSelect}}) {{pageTitle=='Chỉnh sửa gallery'?'photo':pageTitle}} được chọn<i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu :style="{'width': dwStyle.width}" class="dropdown-status-table" slot="dropdown" v-if="actionsTable && actionsTable.length">
          <el-dropdown-item v-for="actionTable of actionsTable" @click="actionTable.callback(multipleSelection)">
            <el-button
              @click="actionTable.callback(multipleSelection)"
              type="text"
              class="full-width text-left"
              :class="actionTable.color"
            >{{actionTable.title}}</el-button>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-table class="eye-table"
                :data="queriedData"
                border
                style="width: 100%"
                @selection-change="handleSelectionChange"
                @sort-change="sortChange"
      >
        <el-table-column
          type="selection"
          width="45"
          v-if="checkAction"
          fixed="left"
          class-name="bg-white"
        >
        </el-table-column>
        <el-table-column v-for="column, i in tableColumns"
                         :key="column.label"
                         :min-width="column.minWidth"
                         :prop="column.prop"
                         :sortable="checkSort"
                         :label="column.label"
                         :fixed="i == 0 ? 'left':false"
                         :class-name="i == 0 ? 'bg-white':''"
        >
           <template slot-scope="props">
             <span v-if="column.type === 'image'">
               <router-link :to="column.link + '/' + props.row['id']" v-if="column.link">
                 <img class="img-table" :src="`${host}/uploads/` + props.row[column.prop]"/>
               </router-link>
               <img class="img-table" :src="`${host}/uploads/` + props.row[column.prop]" v-else/>
             </span>
             <span v-else-if="column.type === 'number'">
               <router-link :to="column.link + '/' + props.row['id']" v-if="column.link">
                 {{formatNumber(props.row[column.prop])}}
               </router-link>
               <span v-else>{{formatNumber(props.row[column.prop])}}</span>
             </span>
             <span v-else-if="column.type === 'badge'">
               <badge :type="parseType(props.row[column.prop], column.prop)">{{parseStatus(props.row[column.prop], column.prop)}}</badge>
             </span>
             <span v-else>
               <router-link :to="column.link + '/' + props.row['id']" v-if="column.link">
                 {{props.row[column.prop]}}
               </router-link>
               <span v-else>{{props.row[column.prop]}}</span>
             </span>
           </template>
        </el-table-column>
        <el-table-column
          :width="width.action"
          fixed="right"
          class-name="td-actions"
          label="Thao tác"
          v-if="actions && actions.length"
          >
          <template slot-scope="props">
            <p-button
              :type="action.type" size="sm" icon @click="action.callback(props.$index, props.row)"
              v-for="action in actions"
            >
              <i :class="action.icon"></i>
            </p-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="col-sm-4">
      <el-select
        v-model="pagination.perPage"
        placeholder="Per page">
        <el-option
          class="select-default"
          v-for="item in pagination.perPageOptions"
          :key="item"
          :label="item + ' nội dung/trang'"
          :value="item">
        </el-option>
      </el-select>
    </div>
    <div class="col-sm-4 pagination-info">
      <p class="text-entries text-center">Từ #{{from + 1}} đến #{{to}} trên tổng số {{total}}</p>
    </div>
    <div class="col-sm-4">
      <p-pagination class="pull-right"
                    v-model="pagination.currentPage"
                    :per-page="pagination.perPage"
                    :total="pagination.total">
      </p-pagination>
    </div>
  </div>
</template>
<script>
  import {Table, TableColumn, Select, Option, Dropdown, Button, DropdownMenu, DropdownItem} from 'element-ui'
  import Badge from './Badge.vue';
  import PPagination from 'src/components/UIComponents/Pagination.vue'
  import {mapState} from 'vuex'

  export default{
    props: ['actions', 'actionsTable', 'columnDefs', 'dataRows', 'noSort'],
    components: {
      ElTable: Table,
      ElTableColumn: TableColumn,
      ElSelect: Select,
      ElOption: Option,
      ElDropdown: Dropdown,
      ElButton: Button,
      ElDropdownMenu: DropdownMenu,
      ElDropdownItem: DropdownItem,
      PPagination,
      Badge
    },
    computed: {
      ...mapState({
        pageTitle: 'pageTitle'
      }),
      pagedData () {
        return this.tableData.slice(this.from, this.to)
      },

      tableData() {
        return this.dataRows;
      },
      width() {
        let actionWidth = 0;
        if (this.actions && this.actions.length) {
          actionWidth = Math.max(this.actions.length * 45 + 20, 90);
        }
        return {
          action: actionWidth
        }
      },
      /***
       * Searches through table data and returns a paginated array.
       * Note that this should not be used for table with a lot of data as it might be slow!
       * Do the search and the pagination on the server and display the data retrieved from server instead.
       * @returns {computed.pagedData}
       */
      queriedData () {
        if (!this.searchQuery) {
          this.pagination.total = this.tableData.length
          return this.pagedData
        }
        let result = this.tableData
          .filter((row) => {
            let isIncluded = false
            for (let key of this.propsToSearch) {
              let rowValue = row[key].toString()
              if (rowValue.includes && rowValue.includes(this.searchQuery)) {
                isIncluded = true
              }
            }
            return isIncluded
          })
        this.pagination.total = result.length
        return result.slice(this.from, this.to)
      },
      to () {
        let highBound = this.from + this.pagination.perPage
        if (this.total < highBound) {
          highBound = this.total
        }
        return highBound
      },
      from () {
        return this.pagination.perPage * (this.pagination.currentPage - 1)
      },
      total () {
        this.pagination.total = this.tableData.length
        return this.tableData.length
      },
      checkSelect(){
        return this.multipleSelection.length;
      },
      checkAction(){
        if (!this.actionsTable) return false;
        return this.actionsTable.length;
      },
      host(){
        return window.BASE_URL;
      },
      checkSort(){
        return this.noSort ? false : 'custom';
      }
    },
    data () {
      return {
        pagination: {
          perPage: 10,
          currentPage: 1,
          perPageOptions: [5, 10, 25, 50],
          total: 0
        },
        searchQuery: '',
        propsToSearch: this.columnDefs.map(col => col.prop),
        tableColumns: this.columnDefs,
        multipleSelection: [],
        dwStyle: {
          left: 0,
          width: 0
        }
      }
    },
    mounted() {
      window['TBL'] = this;
    },
    methods: {
      parseStatus(status, prop = '') {
        switch(status) {
          case 'new': return 'Mới';
          case 'confirm': return 'Đã xác nhận';
          case 'done': return 'Hoàn thành';
          case 'cancel': return 'Hủy';
          case 'return': return 'Hoàn trả';
          case 'draft': return 'Nháp';
          case 'active': return 'Đang hiện';
          case 'inactive': return 'Đang ẩn';
          case 'expried': return 'Hết hạn';
          case 0:
            if (prop == 'payment_status'){
              return 'Chưa thanh toán';
            } else if (prop == 'shipping_status'){
              return 'Chưa giao';
            }
            return 'Chưa phản hồi';
          case 1:
            if (prop == 'payment_status'){
              return 'Đã thanh toán';
            } else if (prop == 'shipping_status'){
              return 'Đang giao';
            }
            return 'Đã phản hồi';
          case 2:
            if (prop == 'shipping_status'){
              return 'Đã giao';
            }
          default: return status;
        }
        return '';
      },
      parseType(type, prop = '') {
        switch(type) {
          case 'new': return 'primary';
          case 'confirm': return 'info';
          case 'done': return 'success';
          case 'cancel': return 'danger';
          case 'return': return 'danger';
          case 'draft': return 'default';
          case 'active': return 'info';
          case 'inactive': return 'warning';
          case 'expried': return 'danger';
          case 0:
            if (prop == 'shipping_status'){
              return 'danger';
            }
            return 'warning';
          case 1:
            if (prop == 'shipping_status'){
              return 'warning';
            }
            return 'info';
          case 2:
            return 'info';
          default: return '';
        }
        return '';
      },
      handleLike (index, row) {
        alert(`Your want to like ${row.name}`)
      },
      handleEdit (index, row) {
        alert(`Your want to edit ${row.name}`)
      },
      handleDelete (index, row) {
        let indexToDelete = this.tableData.findIndex((tableRow) => tableRow.id === row.id)
        if (indexToDelete >= 0) {
          this.tableData.splice(indexToDelete, 1)
        }
      },
      formatNumber(num){
        return num.toString().replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1,");
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;

        let elTable = this.$el.getElementsByClassName('el-table')[0];
        let table = elTable.getElementsByTagName('table')[0];
        let ths = table.getElementsByTagName('th');

        let left = ths[0].offsetWidth;
        let width = ths[1].offsetWidth + ths[2].offsetWidth;

        this.dwStyle.left = left + 15 + 5 + 'px';
        this.dwStyle.width = width - 10 + 'px';
      },
      sortChange(column, prop, order){
        this.$emit('sortChange', column)
      },
      updateColumns(columns) {
        this.tableColumns = columns;
      }
    },
  }
</script>
<style lang="scss">
  .el-table {
    .td-actions {
      background-color: white !important;
      button.btn {
        margin-right: 5px;
      }
    }
    .el-table__row td {
      padding: 5px !important;
    }
  }

  .text-entries {
    line-height: 46px;
  }
  .dropdown-check-table{
    position: absolute;
    top: 8px;
    left: 50px;
    z-index: 999;
  }
  .dropdown-status-table{
    min-width: 200px;
  }
  .img-table{
    max-width: 100px;
    width: 100%;
  }
</style>

<style lang="scss">
  .el-table table > thead > tr > th,
  .el-table table > tbody > tr > th,
  .el-table table > tfoot > tr > th,
  .el-table table > thead > tr > td,
  .el-table table > tbody > tr > td,
  .el-table table > tfoot > tr > td {
    div{
      max-height: 69px;
      overflow-y: auto !important;
      &::-webkit-scrollbar {
        display: none;
      }
    }
    padding: 5px;
    margin: 0;
    .img-table{
      max-height: 65px;
    }
  }
  .p-badge, .p-badge:focus, .p-badge:hover { white-space: nowrap; }
</style>
